import React from "react";

const List=()=>{
  return(
    <td>
        <div>
          <div className="sk_line sk_line_30 sk_line_height_16"></div>
        </div>
      </td>   
   )
}

const LeaderbordSkeleton = (prop) => {

  const isList = prop.isDashboard ? [1, 2, 3, 4, 5] : [1, 2, 3, 4]

  return isList.map(() => {
    return (
      <tr>
        <td>
          <div>
            <div className="sk_line sk_line_50 sk_line_height_16"></div>
          </div>
        </td>
       { !prop.isScreen ?  
        <td>
          <div className="d-flex flex-column w-100">
            <div className="d-flex m_grid_author w-100">
              <div className="sk_line sk_img m-0 me-3"></div>
              <div className="w-50">
                <h3 className="sk_line sk_line_height_16 sk_line_90 m-0 mb-3"></h3>
                <p className="sk_line sk_line_height_10 sk_line_60 m-0"></p>
              </div>
            </div>
          </div>
        </td> : <><List/><List/></> }
        <List/>
        <List/>
        <List/>
        <List/>
      </tr>
    );
  });
};

export default React.memo(LeaderbordSkeleton);
